
const accountKey = 'account_key'

const setAccount = (value) => {
  window.localStorage.setItem(accountKey, value);
}

const getAccount = () => {
  return window.localStorage.getItem(accountKey);
}

const clearAccount = () => {
  window.localStorage.setItem(accountKey, null);
}

const useLocalStorage = () => {
  return {setAccount, getAccount, clearAccount};
}

export default useLocalStorage
