import * as React from 'react';
import LinkedAccounts from './Main/Account/LinkedAccounts';
const OtherSamplePage = React.lazy(() => import('./Main/Other/SamplePage'));
const Dashboard = React.lazy(() => import('./Main/Dashboard/Dashboard'));
const Profile = React.lazy(() => import('./Main/Account/Profile'));
const Referrals = React.lazy(() => import('./Main/Account/Referrals'));
const AddMultipleAccounts = React.lazy(() => import('./Main/Account/AddMultiplaAccounts'));
const Withdrawal = React.lazy(() => import('./Main/Account/Withdrawal'));
const Transfer = React.lazy(() => import('./Main/Account/Transfer'));
const Matrix = React.lazy(() => import('./Main/Account/Matrix'));
const ChangeWallet = React.lazy(() => import('./Main/Account/ChangeWallet'));

let routes = [ 
    { path: '/app', exact: true, name: 'Sample Page', component: OtherSamplePage },
    { path: '/app/sample-page', exact: true, name: 'Sample Page', component: OtherSamplePage },
    { path: '/app/dashboard', exact: true, name: 'Dashboard', component: Dashboard },
    { path: '/app/profile', exact: true, name: 'Profile', component: Profile },
    { path: '/app/linked-accounts', exact: true, name: 'Linked Accounts', component: LinkedAccounts },
    { path: '/app/referrals', exact: true, name: 'Referrals', component: Referrals },
    { path: '/app/add-multiple-accounts', exact: true, name: 'AddMultipleAccounts', component: AddMultipleAccounts },
    { path: '/app/withdraw', exact: true, name: 'Withdrawal', component: Withdrawal },
    { path: '/app/transfer', exact: true, name: 'Transfer', component: Transfer },
    { path: '/app/matrix', exact: true, name: 'Matrix', component: Matrix },
    { path: '/app/change-wallet', exact: true, name: 'ChangeWallet', component: ChangeWallet },
];
export default routes;
