import React, { useEffect, useState } from 'react';
import { Row, Col } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import useBlockchain from '../../hooks/useBlockchain';
import useLocalStorage from '../../hooks/useLocalStorage';

const LinkedAccounts = () => {
    const [accounts, setAccounts] = useState([]);
    const [contract, , active] = useBlockchain();
    const { getAccount, setAccount } = useLocalStorage();
    const history = useHistory();

    useEffect(() => {
        if (!active) return;
        const fn = async () => {
            const user = await contract.getUser(getAccount());
            const accountIDs = await contract.getAccounts(user.addr);
            const res = [];
            for (let i = 0; i < accountIDs.length; i++) {
                const user = await contract.getUser(accountIDs[i]);
                res.push({ id: parseInt(accountIDs[i]), classicLevel: parseInt(user.classicLevel), premiumLevel: parseInt(user.premiumLevel) });
            }
            setAccounts(res);
        };
        fn();
    }, [active, getAccount, setAccounts, contract]);

    const login = (id) => {
        setAccount(id);
        history.push('/app/dashboard');
        window.location.reload();
    };

    const upgradeDownline = async (id) => {
        await contract.upgradeToPremium(id, Math.random());
        window.alert('Done');
    };

    const activateDownline = async (id) => {
        await contract.activate(id);
        window.alert('Done');
    };

    return (
        <>
            <Row>
                <Col md={12}>
                    <div className="card">
                        <div className="card-header">
                            <h5>My Referrals</h5>
                        </div>
                        <div className="card-body table-border-style">
                            <div className="table-responsive">
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <th>ID</th>
                                            <th>Classic Level</th>
                                            <th>Premium Level</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {accounts.map((directReferral, i) => {
                                            return (
                                                <tr key={i}>
                                                    <td>{directReferral.id}</td>
                                                    <td>{directReferral.classicLevel}</td>
                                                    <td>{directReferral.premiumLevel}</td>
                                                    <td>
                                                        <button
                                                            className="btn btn-link btn-xs"
                                                            onClick={() => {
                                                                login(directReferral.id);
                                                            }}
                                                        >
                                                            Login
                                                        </button>
                                                        {directReferral.premiumLevel > 0 ? (
                                                            <a href={`/matrix?id=${directReferral.id}`}>View</a> 
                                                        ) : (
                                                            ''
                                                        )}
                                                        {directReferral.premiumLevel === 0 ? (
                                                            <button onClick={() => upgradeDownline(directReferral.id)}>
                                                                Upgrade to Premium
                                                            </button>
                                                        ) : (
                                                            ''
                                                        )}
                                                        {directReferral.classicCheckpoint === 0 ? (
                                                            <button onClick={() => activateDownline(directReferral.id)}>Activate</button>
                                                        ) : (
                                                            ''
                                                        )}
                                                    </td>
                                                </tr>
                                            );
                                        })}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>
        </>
    );
};

export default LinkedAccounts;
