import * as React from 'react';
import { Dropdown } from 'react-bootstrap';
import DEMO from '../../../../../store/constant';
import useLocalStorage from '../../../../../hooks/useLocalStorage';
import { useHistory } from 'react-router-dom';

const NavRight = (props) => {
    const history = useHistory()
    const { clearAccount } = useLocalStorage()
    const logout = () => {
        clearAccount()
        history.push('/auth/signin')
    }
    const { getAccount } = useLocalStorage()
    const accountID = getAccount()

    return (<>
            <ul className="navbar-nav ml-auto">
                <li>
                    <Dropdown drop={!props.rtlLayout ? 'left' : 'right'} className="dropdown" alignRight={!props.rtlLayout}>
                        <Dropdown.Toggle variant={'link'} id="dropdown-basic">
                            <i className="icon feather icon-user"/>
                        </Dropdown.Toggle>
                        <Dropdown.Menu alignRight className="profile-notification">
                            <div className="pro-head bg-dark">
                                {/* TODO: get user id */}
                                <span>{accountID}</span>
                                <a onClick={logout} href={DEMO.BLANK_LINK} className="dud-logout" title="Logout">
                                    <i className="feather icon-log-out"/>
                                </a>
                            </div>
                        </Dropdown.Menu>
                    </Dropdown>
                </li>
            </ul>
        </>);
};
export default NavRight;
